import axios from 'axios';
import * as types from './ActionTypes';
const apiBaseUrl = 'https://api.redbot.cc/v1/audioplayer';

const GetContentUrlSucceed = (url) => ({
  type: types.GET_CONTENT_URL_SUCCEED,
  url
});

const GetContentUrlFailed = (err) => ({
  type: types.GET_CONTENT_URL_FAILED,
  err
});

const GetAlbumListSucceed = (data) => ({
  type: types.GET_ALBUM_LIST_SUCCEED,
  data
});

const GetAlbumListFailed = (err) => ({
  type: types.GET_ALBUM_LIST_FAILED,
  err
});

export const GetContentUrl = (selectedDisc, trackNum) => {
  const book = selectedDisc.book;
  const disc = selectedDisc.disc;

  return async dispatch => {
    var url = `${apiBaseUrl}/audiourl/${book.partitionKey}/${book.rowKey}/${disc.rowKey}/${trackNum}`;
    await axios.get(url).then(response => {
      dispatch(GetContentUrlSucceed(response.data));

      // axios.get('https://json.geoiplookup.io/').then(response => {
      //   var geoinfo = {};
      //   geoinfo.ip = encodeURI(response.data.ip);
      //   geoinfo.countrycode = encodeURI(response.data.country_code);
      //   geoinfo.region = encodeURI(response.data.region);
      //   geoinfo.district = encodeURI(response.data.district);
      //   geoinfo.city = encodeURI(response.data.city);
      //   geoinfo.isp = encodeURI(response.data.isp);

      //   var logUrl = `/api/Content/LogAccessHistory?`;
      //   logUrl += `bookName=${selectedDisc.bookName}&`;
      //   logUrl += `cdName=${selectedDisc.discName}&`;
      //   logUrl += `trackNum=${trackNum}`;

      //   axios.get(logUrl, {headers: geoinfo}).then(resp => {
      //     console.log('LogAccessHistory', resp.statusText);
      //   }).catch(err => {
      //     console.log('LogAccessHistory', err);
      //   });
      // });
    }).catch(error => {
      console.log(error);
      dispatch(GetContentUrlFailed(error));
    }
    );
  };
};

export const GetBookList = (catalog) => {
  return async dispatch => {
    const url = `${apiBaseUrl}/catalogs/${catalog}`;
    await axios.get(url).then(response => {
      dispatch(GetAlbumListSucceed(response.data));
    }).catch(error => {
      dispatch(GetAlbumListFailed(error.response));
    }
    );
  };
};



