import React from 'react';
import { connect } from 'react-redux';
import { withRouter } from 'react-router';
import Toggle from 'react-toggle';
import * as actions from '../store/ContentActions';
import * as types from '../store/ActionTypes';
import './toggle.css';
import './home.css';
import cookies from 'js-cookie';
import Select from 'react-select';
import { Nav, NavDropdown, Navbar, MenuItem, Alert } from 'react-bootstrap';

const options = [
  { value: '0', label: '0' },
  { value: '1', label: '1' },
  { value: '2', label: '2' },
  { value: '3', label: '3' },
  { value: '4', label: '4' },
  { value: '5', label: '5' },
  { value: '6', label: '6' },
  { value: '7', label: '7' },
  { value: '8', label: '8' },
  { value: '9', label: '9' }
];

export class Home extends React.Component {
  constructor (props) {
    super(props);
    this.GoPlay = this.GoPlay.bind(this);
    this.GoPlayPrevious = this.GoPlayPrevious.bind(this);
    this.GoPlayNext = this.GoPlayNext.bind(this);
    this.toggleLoop = this.toggleLoop.bind(this);
    this.toggleAutoPlayNext = this.toggleAutoPlayNext.bind(this);
    this.DiscSelected = this.DiscSelected.bind(this);
    this.TrackNum1Changed = this.TrackNum1Changed.bind(this);
    this.TrackNum2Changed = this.TrackNum2Changed.bind(this);
    this.AudioEnded = this.AudioEnded.bind(this);

    this.group = this.props.match.params.group;

    var loopClip = cookies.get('loopClip') === undefined ? false : cookies.get('loopClip') === 'true';
    var autoPlayNext = cookies.get('autoPlayNext') === undefined ? false : cookies.get('autoPlayNext') === 'true';

    this.playButtonClicked = false;
    this.state = {
      loop: loopClip,
      autoPlayNext: autoPlayNext,
      selectedDisc: undefined
    };
  }

  TrackNum1Changed (target) {
    console.log(target);
    this.props.StopPlay();
    this.props.SetNewTrackNum(target.value, this.props.trackNum2);
  }

  TrackNum2Changed (target) {
    console.log(target);
    this.props.StopPlay();
    this.props.SetNewTrackNum(this.props.trackNum1, target.value);
  }

  GoPlay (event) {
    var currentTrack = Number(`${this.props.trackNum1}${this.props.trackNum2}`);
    const selectedDisc = this.state.selectedDisc;
    if (currentTrack >= selectedDisc.disc.minTrack && currentTrack <= selectedDisc.disc.maxTrack) {
      this.props.StartPlay();
    } else {
      alert('看一下书上的音轨号码，选一个正确的再按播放按钮');
    }
  }

  GoPlayPrevious () {
    var currentTrack = Number(`${this.props.trackNum1}${this.props.trackNum2}`);
    const newTrack = currentTrack - 1;
    const selectedDisc = this.state.selectedDisc;

    if (newTrack >= selectedDisc.disc.minTrack) {
      const num2 = newTrack % 10;
      const num1 = (newTrack - num2) / 10;
      this.props.SetNewTrackNum(num1, num2);
      this.props.StartPlay();
    } else {
      alert('已近到最前面了，没有了');
    }
  }

  GoPlayNext (displayAlertOnLast = true) {
    var currentTrack = Number(`${this.props.trackNum1}${this.props.trackNum2}`);
    const newTrack = currentTrack + 1;
    const selectedDisc = this.state.selectedDisc;

    if (newTrack <= selectedDisc.disc.maxTrack) {
      const num2 = newTrack % 10;
      const num1 = (newTrack - num2) / 10;
      this.props.SetNewTrackNum(num1, num2);
      this.props.StartPlay();
    } else {
      if (displayAlertOnLast === true) {
        alert('已近到最后面了，没有了');
      }
    }
  }

  AudioEnded () {
    if (this.state.autoPlayNext === true) {
      this.GoPlayNext(false);
    }
  }

  componentDidMount () {
    if (this.props.match.params.group === 'index.html') {
      this.props.history.push('/');
    }
    this.props.GetBookList(this.group === undefined ? '' : this.group);
  }

  componentDidUpdate (prevProps) {
    const selectedDisc = this.state.selectedDisc;
    if (selectedDisc !== undefined) {
      if (this.props.trackNum1 !== prevProps.trackNum1 ||
        this.props.trackNum2 !== prevProps.trackNum2 ||
        this.props.startPlay !== prevProps.startPlay) {
        var currentTrack = Number(`${this.props.trackNum1}${this.props.trackNum2}`);
        if (currentTrack >= selectedDisc.disc.minTrack && currentTrack <= selectedDisc.disc.maxTrack && this.props.startPlay === true) {
          this.props.GetContentUrl(selectedDisc, `${this.props.trackNum1}${this.props.trackNum2}`);
        }
      }
    }
  }

  toggleLoop (event) {
    cookies.set('loopClip', event.target.checked);
    this.setState({loop: event.target.checked});
    if (event.target.checked === true) {
      cookies.set('autoPlayNext', false);
      this.setState({ autoPlayNext: false });
    }
  }

  toggleAutoPlayNext (event) {
    cookies.set('autoPlayNext', event.target.checked);
    this.setState({ autoPlayNext: event.target.checked });
    if (event.target.checked === true) {
      cookies.set('loopClip', false);
      this.setState({ loop: false });
    }
  }

  DiscSelected (event) {
    const selectedDiscItem = JSON.parse(event);
    var book = this.props.metadata.books.find(book => book.rowKey === selectedDiscItem.bookRowKey);
    var disc = this.props.metadata.discs.find(disc => disc.rowKey === selectedDiscItem.discRowKey);
    const selectedDisc = {book, disc};
    this.setState({selectedDisc});
  }

  render () {
    var menuItems = [];
    if (this.props.metadata !== undefined) {
      const books = this.props.metadata.books;
      books.forEach(book => {
        const discs = this.props.metadata.discs.filter(disc => disc.partitionKey === book.rowKey);
        discs.forEach(disc => {
          menuItems.push({
            key: JSON.stringify({bookRowKey: book.rowKey, discRowKey: disc.rowKey}),
            label: `${book.bookName} ${disc.discName}`
          });
        });
      });
    }

    const { loop } = this.state;
    const selectedDisc = this.state.selectedDisc;
    var selectTitle = '加载中...';
    if (this.props.metadata !== undefined && menuItems.length >= 1) {
      selectTitle = (selectedDisc === undefined ? '选择要收听的CD' : `${selectedDisc.book.bookName} ${selectedDisc.disc.discDesc}`);
    }

    return (
      <React.Fragment>
        <Navbar>
          <Nav>
            <NavDropdown eventKey={3} title={selectTitle} id='basic-nav-dropdown' onSelect={this.DiscSelected}>
              {
                menuItems !== undefined &&
                menuItems.map(item =>
                  <MenuItem key={item.key} eventKey={item.key}>{item.label}</MenuItem>
                )
              }
            </NavDropdown>
          </Nav>
        </Navbar>

        {
          (this.props.err !== undefined && this.props.err.data !== undefined) &&
          <Alert bsStyle='danger'>
            <div>哈哈，出了点问题！！</div>
            {this.props.err.data.InnerException.Message}
          </Alert>
        }

        {
          selectedDisc !== undefined &&
          <React.Fragment>
            <div className='main-container'>
              <div className='container'>
                <div className='row'>
                  <div className='col-sm'>
                    <div><label>选择音轨 ({selectedDisc.disc.minTrack} ~ {selectedDisc.disc.maxTrack})</label></div>
                    <table cellPadding='10'>
                      <tbody>
                        <tr valign='middle'>
                          <td width='80px'>
                            <Select
                              value={{value: this.props.trackNum1, label: this.props.trackNum1}}
                              onChange={this.TrackNum1Changed}
                              options={options}
                              isSearchable={false}
                            />
                          </td>
                          <td width='80px'>
                            <Select
                              value={{value: this.props.trackNum2, label: this.props.trackNum2}}
                              onChange={this.TrackNum2Changed}
                              options={options}
                              isSearchable={false}
                            />
                          </td>
                          <td>
                            <svg width='60px' height='30px' viewBox='0 0 60 30' onClick={this.GoPlay}>
                              <rect x='0' y='0' height='30' width='60' style={{stroke: 'DarkGray', fill: '#19AB27'}} />
                              <text x='15' y='20' fill='White' style={{font: 'bold 14px sans-serif'}}>播放</text>
                            </svg>
                          &nbsp;&nbsp;&nbsp;&nbsp;
                            <svg width='20px' height='28px' viewBox='0 0 20 28' onClick={this.GoPlayPrevious}>
                              <polygon fill='#19AB27' points='20,0 4,9.6 4,0 0,0 0,24 4,24 4,14.4 20,24' />
                            </svg>
                          &nbsp;&nbsp;&nbsp;&nbsp;
                            <svg width='20px' height='28px' viewBox='0 0 20 28' onClick={this.GoPlayNext}>
                              <polygon fill='#19AB27' points='16,0 16,9.6 0,0 0,24 16,14.4 16,24 20,24 20,0' />
                            </svg>
                          </td>
                        </tr>
                      </tbody>
                    </table>
                    <div style={{minHeight: '5px'}}>&nbsp;</div>
                  </div>
                  <div className='col-sm'>

                    <table cellPadding='10'>
                      <tbody>
                        <tr valign='middle'>
                          <td nowrap='nowrap'>
                            <label htmlFor='toggle-loop'>循环播放</label>
                          </td>
                          <td width='80px'>
                            <Toggle id='toggle-loop'
                              checked={this.state.loop}
                              onChange={this.toggleLoop} />
                          </td>
                          <td nowrap='nowrap'>
                            <label htmlFor='toggle-autoplay'>连续播放</label>
                          </td>
                          <td width='80px'>
                            <Toggle id='toggle-autoplay'
                              checked={this.state.autoPlayNext}
                              onChange={this.toggleAutoPlayNext} />
                          </td>
                        </tr>
                      </tbody>
                    </table>
                  </div>
                </div>
              </div>
            </div>
            <footer className='footer'>
              <div className='container'>
                <audio
                  src={this.props.contentUrl}
                  preload='auto'
                  loop={loop}
                  autoPlay
                  controls
                  onEnded={this.AudioEnded}
                  style={{width: '99%'}}
                />
              </div>
            </footer>
          </React.Fragment>
        }
      </React.Fragment>
    );
  }
}

const mapStateToProps = (state) => ({
  contentUrl: state.content.url,
  err: state.content.err,
  metadata: state.content.metadata,
  trackNum1: state.content.trackNum1,
  trackNum2: state.content.trackNum2,
  startPlay: state.content.startPlay,
  startPlayTick: state.content.startPlayTick
});

const mapDispatchToProps = dispatch => ({
  GetContentUrl: (selectedDisc, trackNum) =>
    dispatch(actions.GetContentUrl(selectedDisc, trackNum)),
  GetBookList: (group) => dispatch(actions.GetBookList(group)),
  SetNewTrackNum: (trackNum1, trackNum2) => dispatch({type: types.UPDATE_TRACK_NUMS, trackNum1, trackNum2}),
  StartPlay: () => dispatch({type: types.START_PLAY}),
  StopPlay: () => dispatch({type: types.STOP_PLAY})
});

export default connect(mapStateToProps, mapDispatchToProps)(withRouter(Home));



