import * as types from './ActionTypes';

const initialState = {
  url: undefined,
  metadata: undefined,
  err: undefined,
  trackNum1: 0,
  trackNum2: 0,
  startPlay: false,
  startPlayTick: 0
};

export const ContentReducer = (state = initialState, action) => {
  var d = new Date();
  var t = d.getTime();

  switch (action.type) {
    case types.GET_CONTENT_URL_SUCCEED:
      return {
        ...state,
        url: action.url,
        err: undefined
      };
    case types.GET_ALBUM_LIST_SUCCEED:
      return {
        ...state,
        metadata: action.data
      };
    case types.UPDATE_TRACK_NUMS:
      return {
        ...state,
        trackNum1: Number(action.trackNum1),
        trackNum2: Number(action.trackNum2)
      };
    case types.START_PLAY:
      return {
        ...state,
        startPlay: true,
        startPlayTick: t
      };
    case types.STOP_PLAY:
      return {
        ...state,
        startPlay: false,
        startPlayTick: t
      };
    case types.GET_CONTENT_URL_FAILED:
    case types.GET_ALBUM_LIST_FAILED:
      return {
        ...state,
        url: undefined,
        metadata: undefined,
        err: action.err
      };
    default:
      return state;
  }
};



